<template>
	<div class="icons">
		<swiper :options="swiperOption">
			<swiper-slide v-for='(page, index) of pages' :key='index'>
				<div 
				  class="icon"
				  v-for='item of page'
				  :key='item.id'
				>
					<router-link tag='div' :to='item.url' class="icon-img">
						<img class="icon-img-content" :src="item.imgUrl">
					</router-link>
					<p class="icon-desc">{{item.desc}}</p>
				</div>
			</swiper-slide>
		</swiper>
	</div>
</template>

<script type="text/javascript">
export default {
	name: 'HomeIcons',
	props: {
		list: Array
	},
	data () {
		return {
			swiperOption: {
				autoplay: false
			}
		}
	},
	computed: {
		pages (){
			const pages = []
			this.list.forEach((item,index) => {
				const page = Math.floor(index / 4)
				if (!pages[page]) {
					pages[page] = []
				}
				pages[page].push(item)
			})
			return pages
		}
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/varibles.styl'
	@import '~styles/mixins.styl'
	.icons >>> .swiper-container
		height: 0
		padding-bottom: 25%
	.icon
		position: relative
		overflow: hidden
		height: 0
		float: left
		width: 25%
		padding-bottom: 25%
		.icon-img
			position: absolute
			top: 8px
			left: 0
			right: 0
			bottom: 34px
			box-sizing: border-box
			padding: 1px
			.icon-img-content
				display: block
				margin: 0 auto
				height: 100%
		.icon-desc
			position: absolute
			left: 0
			right: 0
			bottom: 0
			height: 2.9rem
			line-height: 2.9rem
			color: $darkTextColor
			text-align: center
			ellipsis()
</style>


